.card {
  padding-top: 44px;
  margin-bottom: 48px;
  position: relative;
  z-index: 1;
}

.card:nth-last-child(2) {
  margin-bottom: 0px;
}

.card .container {
  background-color: white;
  padding: 77px 32px 41px 32px;
  position: relative;
  border-radius: 5px;
}

.card .container .card-icon {
  width: 88px;
  height: 88px;
  position: absolute;
  left: calc(50% - 44px);
  top: -44px;
}

.card .container .card-icon .image {
  width: 88px;
  height: 88px;
  border-radius: 88px;
  background-color: #3a3054;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card .container .title {
  height: 33px;
  margin-bottom: 12px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
  text-align: center;
  color: #34313d;
}

.card .container .text {
  height: 104px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 26px;
  text-align: center;
  color: #9e9aa8;
}

@media only screen and (min-width: 1440px) {
  .card {
    padding-top: 44px;
    margin: 0px;
    position: relative;
    z-index: 1;
    width: 350px;
  }

  .card:nth-of-type(2) {
    top: 44px;
  }

  .card:nth-of-type(3) {
    top: 88px;
  }

  .card .container .title {
    text-align: start;
  }

  .card .container .text {
    text-align: start;
  }

  .card .container .card-icon {
    left: 32px;
  }
}
