.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.header img {
  max-height: 33px;
}

@media only screen and (min-width: 1440px) {
  .header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
  }
}
